import React from 'react';
import styled from 'styled-components';
import { StaticQuery, graphql } from 'gatsby';
import Img from 'gatsby-image';
import { Container } from '@components/global';
import Call2Act from '@ui/call2act';

const Header = () => (
  <StaticQuery
    query={graphql`
      query {
        mainImg: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "earphone4all" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1650) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    `}
    render={data => (
      <HeaderWrapper>
        <Container>
          <Grid>
            <Text>
              <h1>
              Hearing technology for everyone
              </h1>
              <br/>
              <p>
              We're happy to introduce Rearia Hai, the innovative, universal earphone for hearing aid and cochlear implant users, and normal-hearing people.<br/> 
              </p>
              <Call2Act text="Find out more" href="https://rearia.io/hai/"/>
              <br />
            </Text>
            <Art>
              <Img fluid={data.mainImg.childImageSharp.fluid} />
            </Art> 
          </Grid>
        </Container>
      </HeaderWrapper>
    )}
  />
);

const HeaderWrapper = styled.header`
  background-color: ${props => props.theme.color.background.S100};
  padding: 80px;

  @media (max-width: ${props => props.theme.screen.md}) {
    padding: 128px 0 10px 0;
  }
`;

const Art = styled.figure`
  width: 105%;
  margin-left: 0px;
  margin-bottom: 0vh;

  @media (max-width: ${props => props.theme.screen.md}) {
    width: calc(100% + 32px);
    margin-left: -16px;
  }  

  > div {
    width: 100%;
    margin-bottom: -3%;

    @media (max-width: ${props => props.theme.screen.md}) {
      width: 100%;
      margin-top: -50px;
    }
  }
`;

const Grid = styled.div`
  display: grid;
  grid-template-columns: 2fr 3fr;
  align-items: center;
  grid-gap: 64px;

  @media (max-width: ${props => props.theme.screen.md}) {
    grid-template-columns: 1fr;
    grid-gap: 80px;

    > ${Art} {
      order: 2;
    }
  }
`;

const Text = styled.div`
  justify-self: start;
  font-weight: black;
  padding-top: 50px;
  @media (max-width: ${props => props.theme.screen.md}) {
    justify-self: start;
    padding-top: 0px;
  }
`;


export default Header;
